<template>
  <div class="card">
    <div class="card-header">SetupHook</div>
    <div class="card-body">
        <button @click="handleBtn" class="btn btn-info btn-sm mr-2">버튼</button>
        <button @click="emitEvent" class="btn btn-info btn-sm">이벤트 발생</button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
export default {
    //vue2 방식
    //props 선언
    props : ["prop1"],

    //이벤트 선언
    emits : ["component-event"],

    //vue3 방식
    //setup() 훅 재정의
    setup(props, context) {
        console.log("create 상태가 될 때 실행");
        console.log(props);
        //이벤트 선언
        //반응형 속성 선언
        const count = ref(0);
        //이벤트 핸들러 선언 
        function handleBtn(){
            console.log("handleBtn");
        }
        function emitEvent(){
            context.emit("component-event", "value1")
        }

        return {//선언시 리턴해야함
            count,
            handleBtn,
            emitEvent

        }
    },
}
</script>
